html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;

  font-family: Arial, sans-serif;
  font-size: 100px;

  color: #fff;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  height: 300px;
  min-width: 300px;
}

.block--1 {
  background-color: #f00;
  order: 1;
}

.block--2 {
  background-color: #c00;
  order: 2;
}

.block--3 {
  background-color: #900;
  order: 3;
}

.block--4 {
  background-color: #600;
  order: 4;
}

.block--5 {
  background-color: #300;
  order: 5;
}

.block--6 {
  background-color: #000;
  order: 6;
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: calc(100% / 3);
  }

  .block--4 {
    order: 6;
  }

  .block--6 {
    order: 4;
  }
}
